import React, { useState, useEffect } from 'react';
import './Other.css';
import { getOther } from '../../redux/actions/app'
import EventItem from '../../component/eventitem/EventItem'
import GalleryItem from '../../component/galleryitem/GalleryItem'
import { Link } from 'react-router-dom'
import { scrollToTop } from '../../App'
import Zoom from 'react-reveal/Zoom';
import { connect } from 'react-redux'

const Other =  React.memo(
    (props) =>{
        const { lang } = props.app;
        let [other, setOther] = useState([]);
        let [link, setLink] = useState('');
        let [title, setTitle] = useState('');
        useEffect(async ()=>{
            if (props.title == '  ')
                setTitle(lang===undefined||lang==='ru'? '  ' : lang==='kg'? '' : 'EXHIBITIONS & EVENTS')
            else if (props.title == ' ')
                setTitle(lang===undefined||lang==='ru'? ' ' : lang==='kg'? ' ' : 'OTHER EVENTS')
            else if(props.title == '') {
                setTitle(lang===undefined||lang==='ru'? '' : lang==='kg'? '' : 'GALLERY')
            } else if(props.title == '  ') {
                setTitle(lang===undefined||lang==='ru'? '  ' : lang==='kg'? '  ' : 'ARTS BY AUTHOR')
            } else if(props.title == '  ') {
                setTitle(lang===undefined||lang==='ru'? '  ' : lang==='kg'? '  ' : 'ARTS BY GENRE')
            } else if(props.title == '  ') {
                setTitle(lang===undefined||lang==='ru'? '  ' : lang==='kg'? '  ' : 'ARTS BY YEAR')
            }
            },[lang, props.title])
        useEffect(async ()=>{
            let data;
            if(props.title == '  ' || props.title == ' ') {
                await setLink('events');
                data = {name: ''};
            } else if(props.title == '') {
                await setLink('genre')
                data = {name: '', data: {search: '', sort: '', skip: 0}}
            } else if(props.title == '  ') {
                await setLink('genre')
                data = {name: '', data: {search: props.meta, sort: 'author'}}
            } else if(props.title == '  ') {
                await setLink('genre')
                data = {name: '', data: {search: props.meta, sort: 'genre'}}
            } else if(props.title == '  ') {
                await setLink('genre')
                data = {name: '', data: {search: props.meta, sort: 'date'}}
            }
            data = await getOther(data);
            await setOther(data);
        },[props.id])
        return (
            <Zoom>
            <div className='other'>
                <div className='textlinebig'>
                    <Link className='link' to={link} onClick={scrollToTop}>
                        {props.title === '  '?'  ':title}
                    </Link>
                </div>
                <div className={props.title === '  '||props.title === ' '?'other-line other-flex':'other-line'}>
                    {other!=undefined&&other.length>0 ? (other).map((element, idx) => {
                        if(props.title === '  '||props.title === ' '){
                            return (<EventItem element={element} idx={idx} />)
                        } else if(props.title === ''||props.title === '  '||props.title === '  '||props.title === '  '){
                            return (<GalleryItem element={element} idx={idx} />)
                        }
                    })
                    :
                    null
                    }
                </div>
            </div>
            </Zoom>
         );
    }
)

function mapStateToProps (state) {
    return {
        app: state.app
    }
}

export default connect(mapStateToProps)(Other);
